import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { CommonModule } from '@angular/common';
import { RouterModule } from '@angular/router';
import { environment } from 'environments/environment';
import { SharedComponentModule } from '@shared/components/shared-component.module';
import { PwaService } from '@shared/service/app/pwa.service';

@Component({
  selector: 'app-reporte',
  standalone: true,
  imports: [CommonModule, RouterModule, SharedComponentModule],
  templateUrl: './reporte.component.html'
})
export class ReporteComponent implements OnInit {
  reporteUrl: string;

  constructor(private route: ActivatedRoute, private pwaService: PwaService) {}

  ngOnInit(): void {
    this.route.paramMap.subscribe(params => {
      const path = params.get('path');
      if (path) {
        this.reporteUrl = `${environment.baseRest}/${path}`;
      } else {
        console.error('No se proporcion el camino del reporte');
      }
    });
  }
}